.backdrop {
  z-index: 2;
  width: 100%;
  height: 100%;
  transition: opacity var(--trns-modal), visibility var(--trns-modal);
  background-color: rgba(0, 0, 0, .4);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-delay: .15s;
}

.modal {
  width: 335px;
  height: 582px;
  transition: all var(--trns-modal);
  background: #fff;
  border: 1px solid #888;
  border-radius: 15px;
  margin: auto;
  padding: 28px 20px;
  transition-delay: .15s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
