    height: 632px;
    padding: 40px;
  }
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transition-delay: 0s;
  transform: scale(.75)translate(-65%, -65%);
}

.close {
  cursor: pointer;
  float: right;
  cursor: pointer;
  transition: transform var(--trns-modal);
  font-size: 28px;
  font-weight: bold;
  position: fixed;
